
.followUp{
		font-size: 0.3rem;
    height: calc(100% - 70px);
}
.followUp-content{
		height: 100%;
}
.followUp-content-bottom{
		height:calc(100% - 100px);
}
.followUp-content-right{
		min-height: 70%;
}
	/*通用样式*/
input{
		outline: none;
		border: 1px solid #eee;
}
.followUp .detect{
		margin-left: 15px;
}
.chosebtn{
		display: inline-block;
    margin-left: 15px;
    border: 1px solid #3C9BFF;
    line-height: .6rem;
    padding: 0 0.4rem;
    border-radius: 8px;
    color: #3C9BFF;
}
.chosebtn.on{
		display: inline-block;
    margin-left: 15px;
    background:#3C9BFF;
    border: 1px solid #3C9BFF;
    line-height: .6rem;
    padding: 0 0.4rem;
    border-radius: 8px;
    color: #fff;
}
.input-p{
		display: inline-block;
		position: relative;
}
.commoninput{
		height: 0.6rem;
		width: 21vw !important;
		font-size: .28rem;
		padding-left: 15px;
		border-radius: 6px;
		margin-left: 15px;
}
select.commoninput{
		width: calc(21vw + 15px) !important;
    border-color: #eee;
    color: #666;
    appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    background-color: #fff;
}
select.commoninput.substantial{
		color: #333;
}
.biginput{
		height: 60px;
		width: 57vw !important;
		font-size: .28rem;
		padding-left: 15px;
		border-radius: 6px;
		margin-left: 15px;
}
.smallinput{
		height: 60px;
		width: 15vw !important;
		font-size: .28rem;
		padding-left: 15px;
		border-radius: 6px;
		margin-left: 15px;
}
.bigselect{
		height: 100px;
		width: 58vw !important;
		font-size: .28rem;
		padding-left: 15px;
		border-radius: 6px;
		margin-left: 15px;
    vertical-align: top;
    padding-top: 5px;
}
.input-list{
		margin-bottom: 15px;
		position: relative;
}
.one-unit,.two-unit{
		position: absolute;
    display: inline-block;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 3%;
    color: #666666;
}
input.disabled,select.disabled,textarea.disabled{
		background: #fff;
		border: 0;
}
.two-unit{
		right: 3%;
}
.unitdisabled{
		left: 35%;
}
.input-list div{
		position: relative;
}
.list-two>div{
		display: inline-block;
		width: 50%;
}
.list-three>div{
		display: inline-block;
		width: 30%;
}
.list-three>div:nth-child(1){
		width: 40%;
}
.followUp-content-head{
		padding: 15px 30px;
		border-bottom: 1px solid #eee;
}
.followUp-content-head>div{
		display: inline-block;
		width: 50%;
}
.content-head-left>span:nth-child(2){
		color: #999;
		margin-left: 10px;
}
.content-head-right>p{
		display: inline-block;
		vertical-align: middle;
}
.content-head-right>p:nth-child(1){
		margin-right: 10px;
}
.content-head-right{
		text-align: right;
		color: #3C9BFF;
}
.content-head-right img{
		width: .5rem;
		height: .5rem;
		vertical-align: middle;
		margin-right: 10px;
}
.content-head-right span{
		vertical-align: middle;
}
.followUp-content-bottom{
		padding: 15px 30px;
}
.followUp-content-bottom>div{
		display: inline-block;
		vertical-align: text-top;
}
.followUp-content-left{
		width: 9%;
}
.followUp-content-left>div{
		margin-bottom: 10px;
		text-align: right;
}
.followUp-content-left>div span{
		display: inline-block;
    background: #F2F2F2;
    width: 100%;
    text-align: center;
    padding: 10px 0;
    border-radius: 15% 0 0 15%;
    color: #999;
}
.followUp-content-left>div.on span{
		background:rgba(60,155,255,0.1) !important;
		color: #3C9BFF;
}
.followUp-content-right{
		width: 91%;
		border: 1px solid #eee;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
}
	/*通用结束*/
.followUp-headline span{
		display: inline-block;
		min-width: 1.6rem;
		line-height: 50px;
		font-size: 0.3rem;
		border-radius: 30px;
		color: #3C9BFf;
		background: rgba(60,155,255,0.1);
		text-align: center;
		padding: 0 0.4rem;
}
.followUp-headline{
		padding: 15px 15px;
}
.followUp-item{
		padding: 15px 15px;
}
.followUp-item>p{
		display: inline-block;
		width: 20%;
		margin-bottom: 15px;
}
.followUp-item>p img{
		width: .5rem;
		height: .5rem;
		vertical-align: middle;
		margin-right: 10px;
}
.followUp-item>p span{
		vertical-align: middle;
}
.other{
		line-height: 60px;
}
.followUp-head{
		padding: 15px 15px;
}
.followUp-head span{
		vertical-align: middle;
}
.followUp-head .lineleft{
		margin-right: 10px;
}
.visittab{
		display: inline-block;
		width: 2.3rem;
		line-height: 0.55rem;
		font-size: 0.23rem;
		border-radius: 7px;
		color: #fff;
		border: 1px solid #3C9BFF;
		text-align: center;
		color:#3C9BFF ;
		margin-right: 15px;
}
.followUp .cov-vue-date input{
		height: 60px;
		width: 21vw !important;
		font-size: .28rem;
		padding-left: 15px;
		border-radius: 6px;
		margin-left: 15px;
}
